body {
  font-family: "Source Sans Pro", sans-serif; }

input, button, textarea {
  font-family: inherit; }

sup {
  display: inline-block;
  font-size: .5em;
  transform: translateY(-0.8em); }

:root {
  --color-bg: #fff;
  --color-elements: #0f0f0f; }
  :root.theme-dark {
    --color-bg: #0f0f0f;
    --color-elements: #fff; }

body {
  display: flex;
  flex-direction: column;
  font-family: "Source Sans Pro", sans-serif;
  width: 100vw;
  min-width: 375px;
  min-height: 100vh;
  padding-top: 40px;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-bg);
  color: var(--color-elements);
  font-size: 18px;
  line-height: 1.3; }
  @media (min-width: 1024px) {
    body {
      padding-top: 0; } }
  body.left-menu-show {
    padding-top: 79px; }

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden; }

button {
  cursor: pointer; }

.container {
  width: 100%;
  max-width: 1600px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }
  @media (min-width: 768px) and (max-width: 1024px) {
    .container {
      padding-left: 30px;
      padding-right: 30px; } }
  @media (min-width: 1024px) {
    .container {
      padding-left: 65px;
      padding-right: 65px; } }

.content-box {
  width: 100%;
  max-width: 1200px;
  margin-left: auto; }
  @media (min-width: 1024px) {
    .content-box {
      padding-left: 100px; } }
  @media (min-width: 1400px) {
    .content-box {
      padding-left: 0; } }

.loader {
  position: fixed;
  z-index: 99999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-bg); }
  .loader--hidden {
    display: none; }

.button {
  /* T- -16-4 */
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.04em;
  position: relative;
  display: inline-block;
  border: 1px solid var(--color-elements);
  color: var(--color-bg);
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  min-width: 183px;
  transition: color 0.2s ease, background-color 0.2s ease; }
  .button::before {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% + 2px);
    height: calc(200% + 2px);
    background-image: linear-gradient(to bottom, var(--color-elements) 50%, var(--color-bg) 50%);
    transition: transform 0.2s ease;
    content: ""; }
  .button:hover::before {
    transform: translateY(-50%); }
  .button__text-box {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 200%;
    transition: transform 0.2s ease; }
    .button:hover .button__text-box {
      transform: translateY(-100%); }
  .button__text {
    display: block;
    width: 100%;
    height: 100%;
    padding: 14px 32px; }
    .button__text--normal {
      color: var(--color-bg); }
    .button__text--hover {
      position: absolute;
      top: 100%;
      left: 0;
      color: var(--color-elements); }
  .button__icon {
    position: absolute;
    top: 8px;
    left: 10px;
    width: 10px;
    height: 7px;
    transform: translateX(-100px);
    opacity: 0;
    color: var(--color-elements);
    transition-delay: .7s;
    transition: opacity 0.2s ease, transform 0.2s ease; }
    .button:hover .button__icon {
      opacity: 1;
      transform: translate(0); }
  .button__anim-box {
    position: relative;
    display: inline-block;
    padding-left: 30px;
    overflow: hidden; }
  .button__anim-text {
    display: inline-block;
    transform: translateY(100%);
    transition: transform .4s; }
    .button:hover .button__anim-text {
      transform: translateY(0); }

.simple-slider__slide {
  text-align: center; }
  .simple-slider__slide--white {
    background-color: #fff; }

.simple-slider__img--full-width {
  display: block;
  width: 100%; }

.simple-slider__nav-box {
  display: flex;
  justify-content: space-between;
  padding: 25px 0; }
  .simple-slider__nav-box--center {
    justify-content: center; }

.simple-slider__pagination {
  /*  - 20  */
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.02em;
  display: inline-block;
  width: auto; }

.simple-slider__text {
  /*  - 20  */
  font-weight: 300;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.02em;
  margin-left: 20px; }
  @media (min-width: 768px) {
    .simple-slider__text {
      min-width: 360px; } }

.simple-slider__progress {
  position: relative;
  width: 100%;
  height: 1px;
  background-color: #B3B3B3; }
  @media (min-width: 1024px) {
    .simple-slider__progress {
      display: none; } }

.simple-slider__progress-value {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  min-width: 10%;
  background-color: #0f0f0f;
  transition: width 0.2s ease; }

.plan-slider {
  display: flex;
  flex-direction: column-reverse; }
  @media (min-width: 768px) {
    .plan-slider {
      flex-direction: column;
      padding: 30px;
